import { DOCUMENT, Location } from '@angular/common';
import { Component, DestroyRef, inject, OnInit } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  ReactiveFormsModule,
  NonNullableFormBuilder,
  Validators,
} from '@angular/forms';
import { Router, RouterModule } from '@angular/router';
import { ButtonModule } from 'primeng/button';
import { FloatLabel } from 'primeng/floatlabel';
import { InputTextModule } from 'primeng/inputtext';
import { environment } from '../../../../../environments/environment';
import { AuthService } from '../../../../api';
import { ToastService } from '../../../../core/services/toast.service';
import { ApiUrlService } from '../../../../core/services/api-url.service';
import { SelectChangeEvent, SelectModule } from 'primeng/select';
import { ApiSchemaService } from '../../../../core/services/api-schema.service';

@Component({
  selector: 'app-forgot-password',
  imports: [
    RouterModule,
    ReactiveFormsModule,
    FloatLabel,
    ButtonModule,
    InputTextModule,
    SelectModule,
  ],
  templateUrl: './forgot-password.component.html',
  styleUrl: './forgot-password.component.scss',
})
export class ForgotPasswordComponent implements OnInit {
  private readonly apiUrlService = inject(ApiUrlService);
  private readonly authService = inject(AuthService);
  private readonly document = inject(DOCUMENT);
  private readonly fb = inject(NonNullableFormBuilder);
  private readonly location = inject(Location);
  private readonly router = inject(Router);
  protected readonly destroyRef = inject(DestroyRef);
  private readonly toastService = inject(ToastService);
  private readonly apiSchemaService = inject(ApiSchemaService);

  readonly environment = environment;
  readonly apiOptions = environment.apiUrls.map((url) => ({
    label: url,
    value: url,
  }));

  resetPasswordForm = this.fb.group({
    username: this.fb.control('', Validators.required),
    apiUrl: this.fb.control(''),
  });

  currentYear: number = new Date().getFullYear();
  loading = false;
  submitted = false;
  submittedUsername = '';

  ngOnInit(): void {
    this.initializeApiUrl();
  }

  private buildResetUrl(): string {
    if (this.apiSchemaService.usesModernSchema()) {
      return this.document.location.origin;
    }

    // Legacy schema: construct full URL with token placeholder
    const loginPath = this.router
      .createUrlTree(['/auth/login'], {
        queryParams: { token: '{token}' },
      })
      .toString();

    return `${this.document.location.origin}/${this.location.prepareExternalUrl(loginPath)}`;
  }

  onSubmit() {
    if (this.resetPasswordForm.valid) {
      this.loading = true;
      const username = this.resetPasswordForm.value.username as string;
      const resetUrl = this.buildResetUrl();

      this.authService
        .resetPassword({ username, resetUrl })
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe({
          next: () => {
            this.loading = false;
            this.submitted = true;
            this.submittedUsername = username;
            this.toastService.showSuccess(
              'Success',
              'Password reset instructions sent.',
            );
          },
          error: () => {
            this.loading = false;
          },
        });
    }
  }

  onApiUrlChange(event: SelectChangeEvent) {
    this.apiUrlService.setApiUrl(event.value);
  }

  private initializeApiUrl(): void {
    // If API dropdown is not shown, use the first API URL without showing the dropdown
    if (!environment.showApiDropdown) {
      const firstApiUrl = environment.apiUrls[0];
      if (firstApiUrl) {
        this.apiUrlService.setApiUrl(firstApiUrl);
        this.resetPasswordForm.controls.apiUrl.setValue(firstApiUrl);
      }
      return;
    }

    // Only when API dropdown is enabled, initialize it
    const currentUrl = this.apiUrlService.getCurrentApiUrl();
    const firstOptionUrl = this.apiOptions[0]?.value;

    if (!firstOptionUrl) {
      return;
    }

    const isValidOption = this.apiOptions.some(
      (option) => option.value === currentUrl,
    );

    let initialApiUrl: string;
    if (isValidOption) {
      initialApiUrl = currentUrl;
    } else {
      initialApiUrl = firstOptionUrl;
      this.apiUrlService.setApiUrl(initialApiUrl);
    }

    this.resetPasswordForm.get('apiUrl')?.setValue(initialApiUrl);
  }
}
