import { inject, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AuthUserResponse } from '../../api';
import { isDefined } from '../../shared/utils';
import { ClientDataService } from '../../features/clients/services/client-data.service';
import { BroadcastMessageService } from './message.service';
import { ActionType } from '../models/message.model';
import { UserSessionService } from './user-session.service';
import { LocalStorageService } from '../../shared/services/local-storage.service';
import { LOCAL_STORAGE_KEY_SELECTED_SESSION_ID } from '../models';

@Injectable({
  providedIn: 'root',
})
export class UserSessionNavigationService {
  private readonly router = inject(Router);
  private readonly clientDataService = inject(ClientDataService);
  private readonly messageService = inject(BroadcastMessageService);
  private readonly userSessionService = inject(UserSessionService);
  private readonly localStorageService = inject(LocalStorageService);

  private isNavigating = false;

  /**
   * Orchestrates navigation based on the available user sessions.
   * Handles auto-redirection for single-account/single-client users.
   */
  async navigateBasedOnSessions(
    sessions: AuthUserResponse[],
    returnUrl?: string | null,
  ): Promise<boolean> {
    if (this.isNavigating) {
      return false;
    }

    this.isNavigating = true;

    try {
      // If already on context-selection, avoid redundant navigation
      if (this.router.url.includes('context-selection')) {
        return false;
      }

      // Fresh Start: Clear old selection to enforce new flow
      this.localStorageService.removeItem(
        LOCAL_STORAGE_KEY_SELECTED_SESSION_ID,
        true,
      );

      // If returnUrl is provided and valid, we'll pass it to context-selection
      const queryParams =
        returnUrl !== null && returnUrl !== undefined && returnUrl.trim() !== ''
          ? { returnUrl }
          : {};

      // Smart Redirect for Single Account Users
      if (sessions.length === 1) {
        const session = sessions[0];
        const ownerType = session.owner?.type?.toUpperCase();
        const defaultClientId = session.defaultClient?.id;

        // Check if user is CLIENT type and has a default client
        if (ownerType === 'CLIENT' && isDefined(defaultClientId)) {
          // Critical: Set the session ID first so the app knows WHO is logged in contextually
          this.userSessionService.setCurrentUserSession(session.id);

          try {
            await this.clientDataService.setSelectedClientById(defaultClientId);
            this.messageService.sendAction(ActionType.profile_changed, {
              clientId: defaultClientId,
            });
            return await this.router.navigate(['/position']);
          } catch {
            // Fallback to wizard on error
            return await this.router.navigate(['/auth', 'context-selection'], {
              queryParams,
            });
          }
        }
      }

      // Default: Redirect to context-selection wizard
      return await this.router.navigate(['/auth', 'context-selection'], {
        queryParams,
      });
    } finally {
      this.isNavigating = false;
    }
  }
}
