import { Injectable } from '@angular/core';
import { isDefined, isNullOrUndefined, isObject } from '../utils';

@Injectable({
  providedIn: 'root',
})
export class NetworkAnimationService {
  private canvas!: HTMLCanvasElement;
  private ctx!: CanvasRenderingContext2D;
  private particles: Particle[] = [];
  private animationFrameId?: number;
  private readonly mouse = { x: 0, y: 0 };
  private running = false; // Add running flag

  private readonly mouseMoveHandler = (e: MouseEvent) => {
    const rect = this.canvas.getBoundingClientRect();
    const scaleX = this.canvas.width / rect.width;
    const scaleY = this.canvas.height / rect.height;

    this.mouse.x = (e.clientX - rect.left) * scaleX;
    this.mouse.y = (e.clientY - rect.top) * scaleY;
  };

  private readonly touchMoveHandler = (e: TouchEvent) => {
    if (isNullOrUndefined(this.canvas) || !isObject(this.canvas)) return;

    const rect = this.canvas.getBoundingClientRect();
    const scaleX = this.canvas.width / rect.width;
    const scaleY = this.canvas.height / rect.height;

    this.mouse.x = (e.touches[0].clientX - rect.left) * scaleX;
    this.mouse.y = (e.touches[0].clientY - rect.top) * scaleY;
  };

  private readonly resizeHandler = () => {
    if (isNullOrUndefined(this.canvas) || !isObject(this.canvas)) return;

    const oldWidth = this.canvas.width;
    const oldHeight = this.canvas.height;

    this.resizeCanvas();

    // Scale particle positions to new dimensions
    const scaleX = this.canvas.width / oldWidth;
    const scaleY = this.canvas.height / oldHeight;

    this.particles.forEach((particle) => {
      // Scale positions proportionally
      particle.x *= scaleX;
      particle.y *= scaleY;

      // Ensure particles stay within bounds
      if (particle.x < 0) particle.x = 0;
      if (particle.x > this.canvas.width) particle.x = this.canvas.width;
      if (particle.y < 0) particle.y = 0;
      if (particle.y > this.canvas.height) particle.y = this.canvas.height;
    });
  };

  initAnimation(canvas: HTMLCanvasElement) {
    if (isNullOrUndefined(canvas) || !isObject(canvas)) return;

    this.canvas = canvas;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    this.ctx = ctx;
    this.resizeCanvas();

    window.addEventListener('mousemove', this.mouseMoveHandler);
    window.addEventListener('touchmove', this.touchMoveHandler, {
      passive: true,
    });
    window.addEventListener('resize', this.resizeHandler);

    this.running = true;
    this.init();
  }

  private init() {
    if (isNullOrUndefined(this.canvas) || isNullOrUndefined(this.ctx)) return;

    // Clear existing particles
    this.particles = [];

    // Calculate number of particles based on screen size
    const area = this.canvas.width * this.canvas.height;
    const baseArea = 1920 * 1080; // Reference screen size
    const scaleFactor = Math.min(1, area / baseArea);

    // Scale particle counts based on screen size
    const baseParticles = 200;
    const baseFloating = 20;
    const particleCount = Math.floor(baseParticles * scaleFactor);
    const floatingCount = Math.floor(baseFloating * scaleFactor);

    // Create regular particles
    for (let i = 0; i < particleCount; i++) {
      this.particles.push(new Particle(this.canvas, this.particles));
    }

    // Create floating particles
    for (let i = 0; i < floatingCount; i++) {
      this.particles.push(new FloatingParticle(this.canvas, this.particles));
    }

    this.animate();
  }

  private resizeCanvas() {
    // Use window dimensions instead of parent element
    this.canvas.style.width = '100vw';
    this.canvas.style.height = '100vh';
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;
  }

  private animate() {
    if (
      isNullOrUndefined(this.canvas) ||
      isNullOrUndefined(this.ctx) ||
      !this.running
    )
      return;

    // Create a gradient background
    const gradient = this.ctx.createLinearGradient(0, 0, 0, this.canvas.height);
    gradient.addColorStop(0, '#000B1E');
    gradient.addColorStop(1, '#000000');

    this.ctx.fillStyle = gradient;
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    // Update and draw particles
    this.particles.forEach((particle) => {
      particle.update(this.mouse);
      particle.draw(this.ctx);
    });

    // Draw connections
    this.drawConnections();

    this.animationFrameId = requestAnimationFrame(() => this.animate());
  }

  private drawConnections() {
    if (isNullOrUndefined(this.canvas) || isNullOrUndefined(this.ctx)) return;

    for (let i = 0; i < this.particles.length; i++) {
      for (let j = i + 1; j < this.particles.length; j++) {
        const dx = this.particles[i].x - this.particles[j].x;
        const dy = this.particles[i].y - this.particles[j].y;
        const distance = Math.sqrt(dx * dx + dy * dy);

        if (distance > 150 && distance < 350) {
          const opacity = 1 - (distance - 150) / 100;
          this.ctx.beginPath();
          this.ctx.strokeStyle = `rgba(255, 255, 255, ${opacity * 0.3})`;
          this.ctx.lineWidth = 0.5;
          this.ctx.moveTo(this.particles[i].x, this.particles[i].y);
          this.ctx.lineTo(this.particles[j].x, this.particles[j].y);
          this.ctx.stroke();
        }
      }
    }
  }

  // Clean up method to cancel animation when needed
  destroy() {
    this.running = false; // Set running to false first

    if (isDefined(this.animationFrameId)) {
      cancelAnimationFrame(this.animationFrameId);
    }

    window.removeEventListener('mousemove', this.mouseMoveHandler);
    window.removeEventListener('touchmove', this.touchMoveHandler);
    window.removeEventListener('resize', this.resizeHandler);
  }
}

class Particle {
  x: number;
  y: number;
  protected speed = 0.25; // Slower speed
  private direction = Math.random() * Math.PI * 2;
  private readonly radius = Math.random() * 4.5 + 0.5; // Smaller particles
  private readonly baseX: number;
  private readonly baseY: number;

  constructor(
    protected canvas: HTMLCanvasElement,
    protected particles: Particle[],
  ) {
    this.x = this.baseX = Math.random() * canvas.width;
    this.y = this.baseY = Math.random() * canvas.height;
  }

  update(mouse: { x: number; y: number }) {
    // Normal movement
    this.x += Math.cos(this.direction) * this.speed;
    this.y += Math.sin(this.direction) * this.speed;

    // Mouse repulsion - stronger pushing effect
    const dx = mouse.x - this.x;
    const dy = mouse.y - this.y;
    const distance = Math.sqrt(dx * dx + dy * dy);

    if (distance < 150) {
      const force = (200 - distance) / 200;
      const angle = Math.atan2(dy, dx);
      this.x -= Math.cos(angle) * force * 5; // Increased force and consistent direction
      this.y -= Math.sin(angle) * force * 5; // Increased force and consistent direction
    }

    // Stronger particle-to-particle repulsion
    this.particles.forEach((otherParticle) => {
      if (otherParticle !== this) {
        const pdx = otherParticle.x - this.x;
        const pdy = otherParticle.y - this.y;
        const particleDistance = Math.sqrt(pdx * pdx + pdy * pdy);

        if (particleDistance < 100) {
          // Increased repulsion radius
          const repulsionForce = (100 - particleDistance) / 100;
          this.x -= pdx * repulsionForce * 0.1; // Increased force
          this.y -= pdy * repulsionForce * 0.1; // Increased force
        }
      }
    });

    // Return to boundaries
    if (this.x < 0 || this.x > this.canvas.width)
      this.direction = Math.PI - this.direction;
    if (this.y < 0 || this.y > this.canvas.height)
      this.direction = -this.direction;
  }

  draw(ctx: CanvasRenderingContext2D) {
    const gradient = ctx.createRadialGradient(
      this.x,
      this.y,
      0,
      this.x,
      this.y,
      this.radius,
    );
    gradient.addColorStop(0, 'rgba(255, 255, 255, 0.5)');
    gradient.addColorStop(1, 'rgba(255, 255, 255, 0)');

    ctx.beginPath();
    ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2);
    ctx.fillStyle = gradient;
    ctx.fill();
  }
}

class FloatingParticle extends Particle {
  private originalX: number;
  private readonly amplitude = Math.random() * 10 + 1;
  private readonly period = Math.random() * 0.25 + 0.02;
  private readonly phase = Math.random() * Math.PI * 2;

  constructor(canvas: HTMLCanvasElement, particles: Particle[]) {
    super(canvas, particles);
    this.originalX = this.x;
    this.speed = 0.01 + Math.random() * 0.2; // Slower upward movement
  }

  override update(mouse: { x: number; y: number }) {
    // Gentle upward movement with slight horizontal wave
    this.y -= this.speed;
    this.x =
      this.originalX +
      Math.sin(this.y * this.period + this.phase) * this.amplitude;

    // Mouse repulsion - stronger pushing effect
    const dx = mouse.x - this.x;
    const dy = mouse.y - this.y;
    const distance = Math.sqrt(dx * dx + dy * dy);

    if (distance < 400) {
      const force = (200 - distance) / 200;
      const angle = Math.atan2(dy, dx);
      this.x -= Math.cos(angle) * force * 5;
      this.y -= Math.sin(angle) * force * 5;
    }

    // Reset position when reaching top
    if (this.y < 0) {
      this.y = this.canvas.height;
      this.originalX = Math.random() * this.canvas.width;
    }
  }
}
