import { Routes } from '@angular/router';
import { userProfileResolver } from '../../core/resolvers/user-profile.resolver';
import { ContainerComponent } from './components/container/container.component';
import { ContextSelectionWizardComponent } from './components/context-selection-wizard/context-selection-wizard.component';
import { ForgotPasswordComponent } from './components/forgot-password/forgot-password.component';
import { LoginComponent } from './components/login/login.component';
import { ResetPasswordWelcomeComponent } from './components/reset-password-welcome/reset-password-welcome.component';

export const AUTH_ROUTES: Routes = [
  {
    path: '',
    component: ContainerComponent,
    children: [
      { path: 'login', component: LoginComponent },
      {
        path: 'context-selection',
        component: ContextSelectionWizardComponent,
        resolve: {
          userProfile: userProfileResolver,
        },
      },
      { path: 'forgot-password', component: ForgotPasswordComponent },
      {
        path: 'reset-password-welcome',
        component: ResetPasswordWelcomeComponent,
        resolve: {
          userProfile: userProfileResolver,
        },
      },
      { path: '', redirectTo: 'login', pathMatch: 'full' },
    ],
  },
];
